package com.geka000.digitron.common

import androidx.compose.runtime.Composable
import androidx.compose.runtime.mutableStateListOf
import androidx.compose.runtime.remember
import androidx.compose.runtime.rememberCoroutineScope
import com.geka000.digitron.common.model.WavFile
import kotlinx.browser.document
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.launch
import kotlinx.coroutines.withContext

public actual fun getPlatformName(): String {
    return "JavaScript"
}

@Composable
public fun UIShow() {
    val files = remember { mutableStateListOf<WavFile>() }
    val scope = rememberCoroutineScope()
    App(
        files = files,
        onOpenPicker = {
            files.clear()
            document.loadFileFromDisk(
            accept = "audio/wav",
            onLoaded = { loaders ->
                scope.launch {
                    loaders.forEach { withContext(Dispatchers.Default) {
                        it.load()
                    } }
                    val result = loaders.map { WavFile(it.name, it.content) }.toList()
                    files.addAll(result)
                }
            }
        ) },
        onSaveFile = { wavFile ->
            document.downloadFileToDisk(
                filename = "recovered_${wavFile.name}",
                type = "audio/wav",
                content = wavFile.content!!
            )
        }
    )
}