package com.geka000.digitron.common

import androidx.compose.foundation.layout.*
import androidx.compose.foundation.lazy.LazyColumn
import androidx.compose.material.Button
import androidx.compose.runtime.Composable
import androidx.compose.material.Text
import androidx.compose.material.Surface
import androidx.compose.runtime.LaunchedEffect
import androidx.compose.runtime.mutableStateListOf
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.unit.dp
import com.geka000.digitron.common.model.WavFile
import com.geka000.digitron.common.model.WavRecovery
import kotlinx.coroutines.delay

@Composable
internal fun App(
    files: List<WavFile>,
    onOpenPicker: () -> Unit,
    onSaveFile: (WavFile) -> Unit
) {
    val filesState = remember(files.toList()) {
        val list = mutableStateListOf<Pair<String, Boolean?>>()
        files.toList().forEach {
            list.add(it.name to false)
        }
        list
    }
    Surface {
        Box(
            modifier = Modifier.fillMaxSize(),
            contentAlignment = Alignment.TopCenter
        ) {
            Column(horizontalAlignment = Alignment.CenterHorizontally) {
                Spacer(modifier = Modifier.height(64.dp))
                Text("Digitron Wav Recovery")
                Spacer(modifier = Modifier.height(16.dp))
                Button(
                    onClick = onOpenPicker
                ) {
                    Text("Load Files")
                }
                Spacer(modifier = Modifier.height(16.dp))
                LazyColumn {
                    items(filesState.size) { index ->
                        val (name, isRecovered) = filesState[index]
                        val text = when(isRecovered){
                            true -> "Recovered"
                            false -> "Recovering..."
                            null -> "Error"
                        }
                        Text("$name: ${text}")
                    }
                }
            }
        }
    }
    LaunchedEffect(files.toList()) {
        println("files: $files")
        files.forEach { file ->
            if (file.content != null) {
                val result = WavRecovery.getInstance().recover(file.content)
                onSaveFile(WavFile(file.name, result))
                println("pre recovered: ${file.name}")
                println(filesState.size)
                filesState.firstOrNull { it.first == file.name }?.let {
                    println("recovered: ${it.first}")
                    filesState[filesState.indexOf(it)] = file.name to true
                }
            } else {
                filesState.firstOrNull { it.first == file.name }?.let {
                    println("error: ${it.first}")
                    filesState[filesState.indexOf(it)] = file.name to null
                }
            }
            delay(50)
        }
    }
}