package com.geka000.digitron.common

import kotlinx.browser.document
import org.khronos.webgl.ArrayBuffer
import org.khronos.webgl.Uint8Array
import org.w3c.dom.Document
import org.w3c.dom.HTMLAnchorElement
import org.w3c.dom.HTMLInputElement
import org.w3c.dom.url.URL.Companion.createObjectURL
import org.w3c.dom.url.URL.Companion.revokeObjectURL
import org.w3c.files.Blob
import org.w3c.files.BlobPropertyBag
import org.w3c.files.FileReader
import org.w3c.files.get
import kotlin.coroutines.suspendCoroutine


internal fun Document.downloadFileToDisk(
    filename: String,
    type: String,
    content: ByteArray,
) {
    val byteContent = content.map { it.toInt().toByte() }.toTypedArray()
    val array = Uint8Array(byteContent)
    val snapshotBlob = Blob(arrayOf(array), BlobPropertyBag(type))
    val url = createObjectURL(snapshotBlob)
    val tempAnchor = (createElement("a") as HTMLAnchorElement).apply {
        style.display = "none"
        href = url
        download = filename
    }
    document.body!!.append(tempAnchor)
    tempAnchor.click()
    revokeObjectURL(url)
    tempAnchor.remove()
}

internal class FileLoader(
    val name: String,
    val file: Blob
) {
    var content: ByteArray? = null
    var isLoaded: Boolean = false

    suspend fun load() = suspendCoroutine<Unit> { cont ->
        if (isLoaded) {
            cont.resumeWith(Result.success(Unit))
            return@suspendCoroutine
        }
        val reader = FileReader()
        reader.onload = { loadEvt ->
            val content = loadEvt.target.asDynamic().result as ArrayBuffer
            val array: ByteArray = Uint8Array(content).unsafeCast<ByteArray>()
            this.content = array
            this.isLoaded = true
            cont.resumeWith(Result.success(Unit))
            Unit
        }
        reader.onerror = { evnt ->
            this.content = null
            this.isLoaded = false
            cont.resumeWith(Result.failure(evnt.asDynamic().error))
            Unit
        }
        reader.onabort = { evnt ->
            this.content = null
            this.isLoaded = false
            cont.resumeWith(Result.failure(evnt.asDynamic().error))
            Unit
        }
        reader.readAsArrayBuffer(file.asDynamic() as Blob)
    }

    override fun equals(other: Any?): Boolean {
        if (this !== other) return false
        if (this.name !== other.name) return false
        return true
    }

    override fun hashCode(): Int {
        return name.hashCode()
    }

}

internal fun Document.loadFileFromDisk(
    accept: String,
    onLoaded: (List<FileLoader>) -> Unit,
) {
    val tempInput = (createElement("input") as HTMLInputElement).apply {
        type = "file"
        style.display = "none"
        this.accept = accept
        multiple = true
    }

    tempInput.onchange = { changeEvt ->
        val count = (changeEvt.target as HTMLInputElement).size
        val loaders = mutableListOf<FileLoader>()
        for (i in 0 until count) {
            val file = (changeEvt.target as HTMLInputElement).files?.get(i) ?: continue
            val name = file.name
            val blob = file.asDynamic() as Blob
            loaders.add(FileLoader(name, blob))
        }
        onLoaded(loaders)
    }

    body!!.append(tempInput)
    tempInput.click()
    tempInput.remove()
}