package com.geka000.digitron.common.model


internal class WavRecovery {
    companion object {
        private val instance: WavRecovery by lazy { WavRecovery() }
        fun getInstance(): WavRecovery {
            return instance
        }
    }

    private fun String.toByteArray(): ByteArray {
        return this.map { it.code.toByte() }.toTypedArray().toByteArray()
    }

    private fun ByteArray.writeWord(value: Long, offset: Int, size: Int) {
        val bytes = ByteArray(size)
        for (i in 0 until size) {
            val result = (value and (0xFFL shl 8*i) ) shr 8*i
            bytes[i] = result.toByte()
        }

        bytes.copyInto(this, offset, 0, size)
    }

    internal fun recover(input: ByteArray): ByteArray {
        val header = input.sliceArray(0..43)
        val data = input.sliceArray(44 until input.size)
        val fileSize = input.size.toLong()
        header.writeWord(fileSize, 4, 4)

        val waveHeader = "WAVE".toByteArray()
        waveHeader.copyInto(header, 8, 0, 4)

        val fmtHeader = "fmt ".toByteArray()
        fmtHeader.copyInto(header, 12, 0, 4)

        val extension = 16L
        header.writeWord(extension, 16, 4)

        val format = 1L
        header.writeWord(format, 20, 2)

        val channels = 1L
        header.writeWord(channels, 22, 2)

        val sampleRate = 44100L
        header.writeWord(sampleRate, 24, 4)

        val byteRate = sampleRate * channels * 2L
        header.writeWord(byteRate, 28, 4)

        val blockAlign = channels * 2L
        header.writeWord(blockAlign, 32, 2)

        val bitsPerSample = 16L
        header.writeWord(bitsPerSample, 34, 2)

        val dataHeader = "data".toByteArray()
        dataHeader.copyInto(header, 36, 0, 4)

        val dataSize = data.size.toLong()
        header.writeWord(dataSize, 40, 4)

        return header + data
    }

}

